@font-face {
  font-family: 'as3';
  src:
    url('asset/font/as3.ttf?vif2gf') format('truetype'),
    url('asset/font/as3.woff?vif2gf') format('woff'),
    url('asset/font/as3.svg?vif2gf#as3') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="as3--"], [class*=" as3--"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'as3' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.as3--menu:before {
  content: "\e900";
}
.as3--x:before {
  content: "\e901";
}
